'use strict';

var novicell = novicell || {};
var nvcl = nvcl || {};

nvcl.topArea = nvcl.topArea || new function () {
    this.init = function () {

        //Parallax stuff
        var parallax = document.getElementById('js-top-area-parallax');
        var animation = document.getElementById('js-top-area-animation');
        var departments = document.getElementById('js-departments');

        var container = document.getElementById('js-top-area-container');

        if (!parallax || !animation || !departments) {
            return;
        }
        var parallaxInstance = new Parallax(parallax);

        window.addEventListener('scroll', function (e) {
            var rect = animation.getBoundingClientRect();
            container.style.opacity = (rect.bottom / 100) / (rect.height / 100);
        });

        //Trigger CSS Animation
        setTimeout(function () {
            animation.classList.add('top-area--animate');
        }, 1000);

        setTimeout(function () {
            departments.classList.add('departments--animate');
        }, 3000);


    };
};
